import React, { Component } from 'react';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

class SymbolExchangeInputEnabler extends Component {
   constructor(props) {
      super(props);
      this.state = { date: new Date() }
   }

   handleClick = () => {
      this.props.onClick();
   }

   componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  tick() {
    this.setState({
      date: new Date()
    });
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

   render() {
      
      const timeOptions = { hour12: false };
      const timeStr = this.state.date.toLocaleTimeString('en-CA', timeOptions);

      return (
        <Container className='mt-3 md-3'>
            <Row>
                <Col className="my-auto">{timeStr}</Col>
                <Col className="text-right"><Button title="show symbol and excahnge input" className="shadow-none" style={{border: 'none', background: 'none'}} onClick={this.handleClick} ><span role="img" aria-label="eye">&#x1f441;</span></Button></Col>
            </Row>               
        </Container>
      )
   }
}

export default SymbolExchangeInputEnabler;
