import React, { Component } from 'react';
import './QuoteDisplay.css';
import { Button, ProgressBar, OverlayTrigger, Tooltip} from 'react-bootstrap';
import Table from 'react-bootstrap/Table';

class QuoteDisplay extends Component {
   constructor(props) {
      super(props);
      this.state = { showDate: false, showRange: false, showDiff: false }
   }

   toggleShowDate = (status) => {
      status ? this.setState({ showDate: false }) : this.setState({ showDate: true });
   }

   toggleShowRange = (status) => {
      status ? this.setState({ showRange: false }) : this.setState({ showRange: true });
   }

   toggleShowDiff = (status) => {
      status ? this.setState({ showDiff: false }) : this.setState({ showDiff: true });
   }

   render() {
      //const { data } = this.props;
      //console.log("ok");
      //console.log("Show Date: " + this.state.showDate);
      return (
         <Table striped bordered hover responsive="sm" size="sm">
            <DisplayHeader
               showStates={this.state}
               handleToggleShowDate={this.toggleShowDate}
               handleToggleShowRange={this.toggleShowRange}
               handleToggleShowDiff={this.toggleShowDiff}
            />
            <DisplayBody
               rows={this.props.data}
               showStates={this.state}
               handleSubmit={this.props.handleSubmit}
            />
         </Table>
      )
   }
}

const DisplayHeader = props => {
   //console.log(props);
   return (
      <thead>
         <tr className="text-right">
            <th className="text-left" style={{ width: '25%' }}>
            </th>
            <th style={{ width: '25%' }}>
               {props.showStates.showDate ? <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowDate(props.showStates.showDate)}><span role="img" aria-label="Date">&#x1F4C6;</span></Button> : <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowDate(props.showStates.showDate)}><span role="img" aria-label="Time">&#x23F2;</span></Button>}
            </th>
            <th style={{ width: '25%' }}>
               {props.showStates.showRange ? <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowRange(props.showStates.showRange)}><span role="img" aria-label="Range">&#x1F4CA;</span></Button> : <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowRange(props.showStates.showRange)}><span role="img" aria-label="Price">&#x1F4B0;</span></Button>}
            </th>
            <th style={{ width: '25%' }}>{props.showStates.showDiff ? <Button className="shadow-none" style={{ border: 'none', background: 'none', color: 'black', padding: 0 }} onClick={() => props.handleToggleShowDiff(props.showStates.showDiff)}><span>&#x00B1;</span></Button> : <Button className="shadow-none" style={{ border: 'none', background: 'none', color: 'black', padding: 0 }} onClick={() => props.handleToggleShowDiff(props.showStates.showDiff)}><span>%</span></Button>}</th>
         </tr>
      </thead>
   )
};

class DisplayBody extends Component {

   refreshQuote = (exchange, symbol) => {
      this.props.handleSubmit(symbol.toLowerCase(), exchange.toLowerCase(), false, false);
   }

   refreshChart = (exchange, symbol) => {
      this.props.handleSubmit(symbol.toLowerCase(), exchange.toLowerCase(), false, true);
   }

   render() {
      const rows = this.props.rows.map((row, index) => {
         // independent of market state
         const dateOptions = { month: 'numeric', day: 'numeric' };
         const timeOptions = { hour12: false };
         const dayLow = row.regularMarketDayLow.fmt;
         const dayHigh = row.regularMarketDayHigh.fmt;
         const regularPrice = row.regularMarketPrice.fmt;
         const colonIndex = row.symbol.indexOf('.');
         const symbolString = colonIndex > -1 ? row.symbol.slice(0, colonIndex) : row.symbol;
         const currency = row.currency.toUpperCase() === 'CAD' ? "C$" : "$";
         const symColor = colonIndex > -1 ?
            (row.symbol.slice(colonIndex + 1).toLowerCase() === 'to' ? "primary" :
               (row.symbol.slice(colonIndex + 1).toLowerCase() === 'cn' ? "danger" :
                  (row.symbol.slice(colonIndex + 1).toLowerCase() === 'v' ? "dark" :
                     "warning"
                  )
               )
            ) : "success";
         const exchange = colonIndex > -1 ?
            (row.symbol.slice(colonIndex + 1).toLowerCase() === 'to' ? "tsx" :
               (row.symbol.slice(colonIndex + 1).toLowerCase() === 'cn' ? "cse" :
                  (row.symbol.slice(colonIndex + 1).toLowerCase() === 'v' ? "tsxv" :
                     "neo"
                  )
               )
            ) : "us";

         // market state dependent
         let marketTime = row.regularMarketTime.raw;
         let changePercent = row.regularMarketChangePercent.fmt;
         let formattedChange = row.regularMarketChange.fmt;
         let change = row.regularMarketChange.raw;
         let price = row.regularMarketPrice.fmt;
         if ((row.marketState === 'POST' || row.marketState === 'POSTPOST' || row.marketState === 'PREPRE') && row.postMarketTime) {
            marketTime = row.postMarketTime.raw;
            changePercent = row.postMarketChangePercent.fmt;
            formattedChange = row.postMarketChange.fmt;
            change = row.postMarketChange.raw;
            price = row.postMarketPrice.fmt;
         } else if (row.marketState === "PRE" && row.preMarketTime) {
            marketTime = row.preMarketTime.raw;
            changePercent = row.preMarketChangePercent.fmt;
            formattedChange = row.preMarketChange.fmt;
            change = row.preMarketChange.raw;
            price = row.preMarketPrice.fmt;
         }

         const dateStr = new Date(marketTime * 1000).toLocaleDateString('en-CA', dateOptions);
         const timeStr = new Date(marketTime * 1000).toLocaleTimeString('en-CA', timeOptions);
         const textColor = change > 0 ? "text-success" : (change < 0 ? "text-danger" : "text-dark");

         return (

            <tr key={exchange + "-" + symbolString} className="text-right">
               <td className="text-left">
                  <Button className={"text-" + symColor + " font-weight-bold"} style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => this.refreshChart(exchange, symbolString)} ><span role="img" aria-label="Chart">&#x1F4C8;</span></Button>
                  {' '}<Button className={"text-" + symColor + " font-weight-bold"} style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => this.refreshQuote(exchange, symbolString)} >{symbolString}</Button></td>
               <td>
                  {this.props.showStates.showDate ? <span>{dateStr}</span> : <span>{timeStr}</span>}
               </td>
               <td>
                  {this.props.showStates.showRange
                     ? <OverlayTrigger overlay={<Tooltip id={exchange + "-" + symbolString}>{dayLow +"-"+dayHigh}</Tooltip>}>
                        <ProgressBar key={1} variant={symColor} min={dayLow} now={price} max={dayHigh} label={regularPrice}/>
                     </OverlayTrigger>
                     : <span>{currency}{price}</span>}
               </td>
               <td className={textColor}>
                  {this.props.showStates.showDiff ? <span>{formattedChange}</span> : <span>{changePercent}</span>}
               </td>
            </tr>
         )
      });

      return <tbody>{rows}</tbody>;
   }
}

export default QuoteDisplay;
